.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 1.27vh;
  margin-bottom: 2.55vh;
}

.main__name {
  position: relative;
}

.main__name_adaptation {
  width: 25.37vw;
  height: 275px;
  border-radius: 10px;
}

.main__name-style {
  width: 100%;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  height: 26px;
  position: absolute;
  /* top: 89.7%; */
  bottom: 2px;
  background-color: rgba(255, 255, 255, 0.75);
  color: #000000;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  padding-top: 4px;
  box-sizing: border-box;
  text-align: left;
  padding-left: 14px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.header__inputs > input,
.header__inputs > select {
  color: white;
}
.header__inputs > select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: none;
  /* background-color: transparent; */
  padding-right: 1rem;
}

.option_disable {
  background: rgba(255, 0, 0, 0.15);
}
